import React from 'react';
import Barcode from 'react-barcode';
import './waybill.css'
import Logo from '../../assets/icons/site-logo.png'

const WayBill = ({data}) => (
    <page id='page'>
        <div className="waybill-header">
            <div className="header-ar">
                <span> </span>
                <span>.: 920033216</span>
                <span>: 0533462946 - 0508752514</span>
                <span> : 310160050100003</span>
                <span>2019/06/22</span>
            </div>
            <div className="header-center">
                <img className="header-logo" src={Logo}/>
                <h3> </h3>
            </div>
            <div className="header-en en">
                <span>Shahina Company</span>
                <span>C.R.: 920033216</span>
                <span>Mob.: 0533462946 - 0508752514</span>
                <span>VAT: 310160050100003</span>
                <span>2019/06/22</span>
            </div>
        </div>

        <div className='barcode'>
            <Barcode value={data.awbNumber} height={25} fontSize={20}/>
        </div>

        <div className="waybill-body">
            <div className="to-text">
                <span> /</span>
                <span className='client-name'>{data.corporate.arabicName} </span>
                <span className="en">Messrs / </span>
            </div>

            <div className="truck-details">
                <h3 className='section-title'>    </h3>
                <div><span className="label"> </span><span className="var">{data.driver.truckType}</span><span className="label en">Truck Type</span></div>
                <div><span className="label"> </span><span className="var">{data.driver.plateNo}</span><span className="label en">Plate No.</span></div>
                <div><span className="label"> </span><span className="var">{`${data.driver.firstName} ${data.driver.lastName}`}</span><span className="label en">Driver Name</span></div>
                <div><span className="label">  </span><span className="var">{data.driver.licenseNumber}</span><span className="label en">License No.</span></div>
                <div><span className="label"> </span><span className="var">{data.driver.iqamaNumber}</span><span className="label en">Iqama No.</span></div>
                <div><span className="label">  </span><span className="var"></span><span className="label en">Operation License No.</span></div>
                <div><span className="label">  </span><span className="var">{data.driver.truckOwnerName}</span><span className="label en">Truck Owner Name</span></div>
            </div>

            <div>
                <h3 className='section-title'> </h3>
                <table>
                    <tbody>
                    <tr>
                        <td rowSpan="2" className="multiline">
                            <span> </span>
                            <span>Load Type</span>
                        </td>
                        <td rowSpan="2" className="multiline">
                            <span> </span>
                            <span>Weight</span>
                        </td> 
                        <td colSpan="2">
                            <span> Direction</span>
                        </td>
                        <td colSpan="3">
                            <span> Rent</span>
                        </td>
                    </tr>
                    <tr>
                        <td className="multiline">
                            <span></span>
                            <span>From</span>
                        </td>
                        <td className="multiline">
                            <span></span>
                            <span>To</span>
                        </td>
                        <td className="multiline">
                            <span></span>
                            <span>Amount</span>
                        </td>
                        <td className="multiline">
                            <span> </span>
                            <span>From Office</span>
                        </td>
                        <td className="multiline">
                            <span> </span>
                            <span>From Customer</span>
                        </td>
                    </tr>
                    <tr>
                        <td>{data.loadType}</td>
                        <td>{data.weight}</td> 
                        <td>{data.origin}</td>
                        <td>{data.destination}</td>
                        <td>{data.amount}</td>
                        <td></td>
                        <td></td>
                    </tr>
                    </tbody>
                </table>
            </div>
            <div className="package-date">
                <div><span className="label">    </span><span>{""+(new Date()).toLocaleDateString("enSA", {  year: 'numeric', month: 'numeric', day: 'numeric'})}</span></div>
                <div><span className="label">   </span><span></span></div>
            </div>

            <div>
                <h3 className='section-title'></h3>
                <ol className="terms">
                    <li>      .</li>
                    <li>     .</li>
                    <li>                .</li>
                    <li>                .</li>
                    <li>          .</li>
                    <li>             .</li>
                    <li>                  .</li>
                </ol>
            </div>
        </div>
        <div className="driver-details">
            <div>
                <div className="name">
                    <span className='label'> </span>
                    <span>{`${data.driver.firstName} ${data.driver.lastName}`}</span>
                </div>

                <div className='singature'>
                    <span className='label'></span>
                    <span></span>
                </div>
            </div>
            <div>
                <div className="name">
                    <span className='label'> </span>
                    <span>{`${data.loggedInUserName.firstName} ${data.loggedInUserName.lastName}`}</span>
                </div>

                <div className='singature'>
                    <span className='label'></span>
                    <span></span>
                </div>
            </div>
        </div>
        <div className='mark'>
            <span className='label'></span>
        </div>

        <div className='waybill-footer'>
            <span>+966920033216 - info@shahina.co - http://www.shahina.co - TIN: 1010893981</span>
            <br/>
            <span>Kingdom of Saudi Arabia - Postal Box - 14262 Additional Number 6329 - Phone / 966920033216 - Saudi Investment Bank</span>
            <br/>
            <span>Account Number - IBAN Number: SA16 6500 0000 1274 9952 0001</span>
        </div>
    </page>
)

export default WayBill;